import React, { Component } from 'react'; 
import TextArea from './Components/TextArea';
import { Graphviz } from 'graphviz-react';
import { interprete } from './docs/interprete';
import { grammar } from './docs/grammar';

class App extends Component {

  constructor(props) {
    super(props);
    this.child1 = React.createRef();
    this.child2 = React.createRef();
    this.text1 = "";
    this.text2 = "";
    this.data = [];
  }

  translate_input = (e) => {
    console.log(this.text1);
    grammar.parse(this.text1);
    this.data = [];
    try{
      this.data[0] = localStorage.getItem('traduccion');
      this.data[1] = localStorage.getItem('st');
      this.data[2] = localStorage.getItem('errores');
      localStorage.removeItem('traduccion');
      localStorage.removeItem('st');
      localStorage.removeItem('errores');
      this.child2.current.modify_text(this.data[0]);
    }catch(error){}


  }

  ejecutar_input = (e) => {
    console.log(this.text2);

    interprete.parse(this.text2);

  }

  report_errors = (e) => {
    console.log("ingreso");
  }

  report_symbols = (e) => {
    console.log("ingreso");
  }

  report_ast = (e) => {
    console.log("ingreso");
  }

  get_text_translate = (texto) => {
    this.text1 = texto;
  }

  get_text_ejecute = (texto) => {
    this.text2 = texto;
  }

  state = { showing: false };

  render () {
    const { showing } = this.state;
    let ast = `digraph G {start -> a0;start -> b0;}`
    return (
      <div className="jumbotron">
        <div>
          <ul className="nav nav-pills">
            <li className="nav-item">
              <a className="nav-link active" role="button" onClick={this.translate_input}>Traducir</a>
            </li>
            <li className="nav-item">
              <a className="nav-link active" role="button"  onClick={this.ejecutar_input}>Ejecutar</a>
            </li>
            <li className="nav-item dropdown">
              <a className="nav-link dropdown-toggle" role="button" data-toggle="dropdown" href="#"  aria-haspopup="true" aria-expanded="false">Reportes</a>
              <div className="dropdown-menu" >
                <a className="dropdown-item" onClick={this.report_errors}>Reporte de errores</a>
                <a className="dropdown-item" onClick={this.report_symbols}>Reporte de tabla de símbolos</a>
                <a className="dropdown-item" onClick={this.report_ast}>Reporte de AST</a>
              </div>
            </li>
          </ul>
        </div>

        <div className="card border-primary mb-3">
        <div className="card-body">
          <div className="row">
            <div className="col">
              <div className="card-header">Translate</div>
              <div className="card-body">
                <TextArea get_text = {this.get_text_translate} ref={this.child1} ></TextArea>
              </div> 
            </div>
            <div className="col">
            <div className="card-header">Ejecute</div>
              <div className="card-body">
                <TextArea get_text = {this.get_text_ejecute} ref={this.child2}></TextArea>
              </div>
            </div>
          </div>
          </div>
        </div>


        <div className="card border-primary mb-3">
          <div className="card-header">Árbol de sintaxis abstracta</div>
          <button className="btn btn-secondary" onClick={() => this.setState({ showing: !showing })}>Show AST</button>
          <div className="card-body">
            { showing 
              ? <div>
                  <Graphviz dot={ast} />
              </div>
              : null
            }

          </div>
        </div>

      </div>
    );
  }
}

export default App;
